import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from './../../http/custom-operation/custom-operation.http.service';
import { Observable } from 'rxjs/Rx';
import { GroupRateInfoBean } from './../bean/custom-group.bean';
import { ConfirmDialogModule, ConfirmationService, Message, SelectItem } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';
import * as _ from 'lodash';
import * as moment from 'moment';
import { SelectGroupRateInfoBean } from './bean/selectGroupRateInfo.bean';
import { tableMessage } from '../../../../../@uisftech/common/constant/config';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
@Component({
  selector: 'app-prefer-rate-scheme',
  templateUrl: './prefer-rate-scheme.component.html',
  styleUrls: ['./prefer-rate-scheme.component.scss'],
  providers: [CusOperationHttpService, ConfirmationService, Commfunc]
})
export class PreferRateSchemeComponent implements OnInit {

  groupIdMatch: boolean = false;//
  code: any;
  first: number = 0;
  //公共
  tableMesg: any = tableMessage;
  statusOptions: any;//优惠状态
  discountTypeOptions: any;//优惠利率方式
  discountScopeOptions: any;//优惠利率幅度

  total: string;
  dataList: any;

  preferRateScheme: string = "优惠利率方案";
  arr: any;
  cars: any;

  //router传入值
  groupId: any;
  groupName: any;

  //模态框
  addDisplay: boolean;//新增
  inValue: any = {};
  addUpHeader: any = "优惠利率新增";
  listNum: any = [];

  detailDisplay: boolean;//查看
  inValue2: any = {};//对象
  detailHeader: any = "优惠利率详情";
  list: any = [];//列表

  updateDisplay: boolean;//查看
  inValue3: any = {};//对象
  updateHeader: any = "优惠利率修改";
  list3: any = [];//列表

  //控件
  selectedValues: SelectItem[] = [];//单选数组
  allVal: any = true;//全选值
  selectedAllVal: any;//全选数组
  flag: boolean = false;//中间量
  msgs: Message[] = [];
  selectGroupRateInfoBean: SelectGroupRateInfoBean = new SelectGroupRateInfoBean();
  groupRateInfo: GroupRateInfoBean = new GroupRateInfoBean();//客户群优惠利率


  params: any;
  ownerName: string;//群主
  groupType: string;
  ownerId: string;

  closeValue: boolean = true;//关闭传值


  //获取所有码值


  constructor(
    public httpService: CusOperationHttpService,
    public routerInfo: ActivatedRoute,
    public confirmationService: ConfirmationService,
    public router: Router,
    public commfunc: Commfunc
  ) {


  }

  ngOnInit() {
    this.codeValues();
    this.statusOptions = this.code['DiscountStatus'];
    this.discountTypeOptions = this.code['DiscountType'];
    this.discountScopeOptions = this.code['DiscountScope'];
    this.selectGroupRateInfoBean.pageSize = 10;
    this.selectGroupRateInfoBean.pageNum = 1;
    this.preSet();

    this.selectGroupRateInfo();// 客户群优惠利率查询
    this.selectCustGroupInfo();
  }
  preSet() {

    if (this.routerInfo.snapshot.params) {
      this.params = this.routerInfo.snapshot.params;
    }
    this.groupId = this.params.groupId;
    this.groupType = this.params.groupType;
    // this.groupName = this.params.groupName;
    // this.ownerName = this.params.ownerName;

    this.groupRateInfo.groupId = this.groupId;//传入参数

    // this.groupRateInfo.productCode = '8';
    this.groupRateInfo.pageNum = 1;
    this.groupRateInfo.pageSize = 10;
  }
  selectGroupRateInfo() {
    this.first = 0;
    // 客户群优惠利率查询
    this.selectGroupRateInfoBean.groupId = this.groupId;
    this.httpService.selectGroupRateInfo(this.selectGroupRateInfoBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.rateList != null && data.rateList != '') {
          data.rateList.forEach((element, i) => {
            if (element.discountScope && element.discountScope != '') {
              let str = '000' + element.discountScope;
              data.rateList[i].discountScope = str.substr(str.length - 3, 3);
            } else {
              data.rateList[i].discountScope = '0';
            }
          });
        }
        console.log(data.rateList);
        this.dataList = data.rateList;
        // debugger
        this.dataList.forEach(element => {
          let now = moment().format('Y-MM-DD');
          if (element.statusId != '1' || element.thruDate < now) {
            element.statusFlag = '1';
          } else {
            element.statusFlag = '0';
          }
        });
        this.total = data.totalNum;
      }
      else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: 'data.returnCode.code' }]
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
    })
  }
  selectCustGroupInfo() {
    let params = {
      groupId: this.groupId
    }
    this.httpService.selectCustGroupInfo(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.groupName = data.groupName;
        this.ownerName = data.ownerName;
        this.groupRateInfo.groupName = this.groupName;
        this.preferRateScheme = this.groupName + ' - ' + this.preferRateScheme;
        this.ownerId = data.ownerId;
        let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.ownerId == mesg.userId) {
          this.groupIdMatch = true;
        } else {
          this.groupIdMatch = false;
        }
      }
      else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: 'data.returnCode.code' }]
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
    })
  }

  ngOnChanges() {

  }
  addCall(event) {
    //添加编辑后的结果
    this.addDisplay = false;
    this.selectGroupRateInfo();

  }

  create() {
    //点击创建按钮
    this.inValue = {
      groupName: this.groupName, groupId: this.groupId, groupType: this.groupType
    };//把bean传入新增
    this.addUpHeader = '优惠利率新增';
    this.addDisplay = true;
    this.listNum = this.dataList;
  }

  check(data) {
    //点击操作按钮中的查看按钮
    this.inValue2 = this.groupRateInfo;//把bean传入查看详情
    this.detailHeader = '优惠利率详情';
    this.detailDisplay = true;
    this.list = data;
  }
  detailCall(event) {
    // 关闭查看详情
    this.detailDisplay = event;
    this.selectGroupRateInfo();
  }
  update(data) {
    this.list3 = data;
    this.list = this.list3;
    this.inValue3 = this.groupRateInfo;//把bean传入查看详情
    this.updateHeader = '优惠利率修改';
    this.updateDisplay = true;
  }
  updateCall(event) {
    this.updateDisplay = event;
    this.selectGroupRateInfo();
  }
  paginate(event) {
    // 分页
    let rows = event.rows;
    this.selectGroupRateInfoBean.pageSize = rows;//每页显示的条数
    let currentPage = event.page + 1;//当前页
    this.selectGroupRateInfoBean.pageNum = currentPage;
    let total = Number(this.total);
    this.first = event.page * event.rows;
    this.selectGroupRateInfo();//客户群优惠利率查询
  }
  delete(item) {
    this.confirmationService.confirm({
      message: '是否确认删除?',
      header: '删除',
      icon: 'fa fa-trash',
      accept: () => {
        let param = {
          rateNum: item.rateNum,
          groupId: item.groupId,
          productCode: item.productCode
        }
        this.httpService.deleteGroupRateInfo(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功' });
            this.selectGroupRateInfo();
          }
          else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }]
          }
        }, err => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
      },
      reject: () => {
        this.msgs = [{ severity: 'info', summary: '提示', detail: '你已取消删除' }];
      }
    });

  }
  proshowThis() {
    this.selectGroupRateInfo();
  }
  fanhui() {
    let groupId = this.commfunc.compileStr(this.groupId);
    this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: groupId }]);
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
  addproshowThis() {

    this.closeValue = true;
  }
  addHidden() {

    this.closeValue = false;
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //登录人和群主的匹配
  // match(){
  //   let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
  //   if(this.ownerId == mesg.userId){
  //     this.groupIdMatch = true;
  //   }   
  // }
}
