import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { InterestRateApproveBean } from './bean/interest-rate-approve.bean';
import { ActivatedRoute } from '@angular/router';
import { ChangeDetailBean } from './bean/interest-rate-approve.bean';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';

@Component({
  selector: 'interest-rate-approve',
  templateUrl: './interest-rate-approve.component.html',
  styleUrls: ['./interest-rate-approve.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class InterestRateApproveComponent implements OnInit {
  code: any;
  msgs: any = [];
  btnText: string;
  discountTypeOptions: any;//优惠利率方式
  discountScopeOptions: any;//优惠利率幅度
  groupTypeOptions: any;//群类型
  statusOptions: any;//优惠状态
  changeDetailBean: ChangeDetailBean = new ChangeDetailBean();
  interestRateApproveBean: InterestRateApproveBean = new InterestRateApproveBean();
  constructor(
    public httpService: CusOperationHttpService,
    public routeInfo: ActivatedRoute,
    public commfunc: Commfunc,
  ) { }

  ngOnInit() {
    this.codeValues();
    this.groupTypeOptions = this.code['GroupTypeSave'];
    this.discountTypeOptions = this.code['DiscountType'];
    this.discountScopeOptions = this.code['DiscountScope'];
    this.statusOptions = this.code['DiscountStatus'];
    this.interestRateApproveBean.statusId = this.routeInfo.snapshot.params['statusId'],
      this.interestRateApproveBean.rateNum = this.routeInfo.snapshot.params['rateNum'],
      this.httpService.selectGroupRateInfo(this.interestRateApproveBean).subscribe(data => {
        if (data.returnCode.code == 'success') {
          console.log(data)
          if(data.rateList != null && data.rateList != ''){
            data.rateList.forEach((element,i) => {
              if(element.discountScope && element.discountScope != ''){
                let str = '000' + element.discountScope;
                data.rateList[i].discountScope = str.substr(str.length-3,3);
              }else{
                data.rateList[i].discountScope = '0';
              }
            });
          }
          this.changeDetailBean = data.rateList[0];
        }
        else {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: 'data.returnCode.code' }]
        }
      }, err => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
      })
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
}
