import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { Message, SelectItem } from 'primeng/primeng';
import { GroupRateInfoBean } from './../../bean/custom-group.bean';
import { AddGroupRateInfoBean } from './bean/addGroupRateInfo.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import * as moment from 'moment';
import { PercentPipe, DecimalPipe } from '@angular/common';
import { CALENDAR_CN } from '../../../../../../@uisftech/common/constant/config';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { checkZzNum, checkNumber, decimalInteger } from '../../../../../../@uisftech/common/service/validator';
@Component({
  selector: 'app-create-rate-scheme',
  templateUrl: './create-rate-scheme.component.html',
  styleUrls: ['./create-rate-scheme.component.scss'],
  providers: [Commfunc, PercentPipe, DecimalPipe]
})
export class CreateRateSchemeComponent implements OnInit, OnChanges {
  code: any;
  isCatelog: boolean = true;
  inValue2: string = 'categrouplog';//产品目录入参
  dateshow: any;
  showtype: number;
  percentShow: number;
  arrTemp: any = [];
  userform: FormGroup;
  discountTypeOptions: any;//优惠利率方式
  discountScopeOptions: any;//优惠利率幅度
  groupTypeOptions: any;
  groupTypeName: string;
  uploadedFiles: any = [];
  date: Date = new Date();
  productPage: boolean = false;
  ch: any = CALENDAR_CN;
  user: any;
  user2: any;
  orgId: string = '';
  userId: string = '';
  userName: string = '';
  orgName: string = '';

  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input() listNum: any;
  @Input() closeValue: any;
  btnText: string;
  addGroupRateInfoBean: AddGroupRateInfoBean = new AddGroupRateInfoBean();
  groupRateInfoBean: GroupRateInfoBean = new GroupRateInfoBean();
  // 中间量
  productName: any;//产品名称
  productCode: any;//产品码
  interestRate: string = '';
  planNum: string = '';
  planAmt: string = '';
  schemeDesc: string = '';
  createrName: string = '';
  createDate: any;
  thruDate: any;
  filePath: string = '';

  // 组件
  msgs: Message[] = [];
  thruDateSel: any = '';
  fileNameFlag: boolean = true;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
    public percentPipe: PercentPipe,
    public decimalPipe: DecimalPipe,
  ) {
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    if (this.user.orgId) {
      this.orgId = this.user.orgId;
    }
    if (this.user.userId) {
      this.userId = this.user.userId;
    }
    if (this.user2.userName) {
      this.userName = this.user2.userName;
    }
    if (this.user2.orgName) {
      this.orgName = this.user2.orgName;
    }

    this.userform = fb.group({
      groupId: new FormControl('', Validators.required),
      productName: new FormControl('', Validators.required),
      groupName: new FormControl(''),
      groupTypeName: new FormControl(''),
      discountType: new FormControl('', Validators.required),
      discountScope: new FormControl(''),
      planNum: new FormControl('', [checkZzNum, Validators.required]),
      planAmt: new FormControl('', [checkZzNum, Validators.required]),
      thruDate: new FormControl('', [Validators.required]),
      schemeDesc: new FormControl('', Validators.required),
      createrName: new FormControl(''),
      createDate: new FormControl(''),
    })
  }

  ngOnInit() {
    this.codeValues();
    this.groupTypeOptions = this.code['GroupTypeSave'];
    this.discountTypeOptions = this.code['DiscountType'];
    this.discountScopeOptions = this.code['DiscountScope'];
    this.showtype = 1;
    this.percentShow = 0;
    let ddate = moment().add(6, 'month').format('Y-MM-DD');
    this.thruDate = new Date(ddate);

    // this.thruDate= ddate;
    console.log(this.thruDate)
    this.addGroupRateInfoBean.createrName = this.userName;
    this.createDate = this.date;
    console.log(this.inValue)
    this.groupTypeOptions.forEach(item => {
      if (item.value == this.inValue.groupType) {
        this.groupTypeName = item.label;
      }
    });

  }
  ngOnChanges() {
    if (this.closeValue == false) {
      this.reset();
    }

  }
  onBasicUpload(event) {
    if (event.xhr.response) { //上传成功 后台返回的 文件路径 和文件名
      this.uploadedFiles = JSON.parse(event.xhr.response);

      if (this.uploadedFiles.hasOwnProperty('errorMsg')) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: this.uploadedFiles.errorMsg });
        return this.fileNameFlag = false;
      }
    }
  }
  addGroupRateInfo() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    // 新增
    let flag = false;
    // for (let item of this.listNum) {
    //   if (item.productCode == this.productCode) {
    //     this.msgs = [];
    //     this.msgs.push({ severity: 'error', summary: '提示', detail: '当前新增产品已存在,请重新选择' });
    //     return flag = true;
    //   }
    // }
    if (!flag) {
      let timethis = moment().add(2, 'year').format('Y-MM-DD');
      let datethis = this.commfunc.transDateN(this.thruDate)
      if (datethis > timethis) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '优惠利率截至日期不能大于' + timethis });
        return;
      } else {
        // this.addGroupRateInfoBean.discountScope=this.addGroupRateInfoBean.discountScope.split('').slice(0,-1).join('').toString();
        // console.log(this.addGroupRateInfoBean.discountScope)
        this.addGroupRateInfoBean.productName = this.productName;
        this.addGroupRateInfoBean.productCode = this.productCode;
        this.addGroupRateInfoBean.thruDate = this.commfunc.transDateN(this.thruDate);
        this.addGroupRateInfoBean.createDate = this.commfunc.transDateN(this.createDate);
        this.addGroupRateInfoBean.groupId = this.inValue.groupId;
        this.addGroupRateInfoBean.groupName = this.inValue.groupName;
        this.httpService.addGroupRateInfo(this.addGroupRateInfoBean).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '新增成功' });
            this.outValue.emit(false);
            this.reset();
          }
          else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
          }
        }, err => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
      }
    }
  }
  productshow() {
    this.productPage = true;
  }
  productCall(event) {
    console.log(event)
    this.productPage = false;
    if (event.length > 1) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '只能选择一个产品' });
    } else {
      this.productName = event[0].productName;
      this.productCode = event[0].productId;
    }
  }
  //重置
  reset() {
    this.productName = '';
    this.productCode = '';
    this.addGroupRateInfoBean.productCode = '';
    this.addGroupRateInfoBean.productName = '';
    this.addGroupRateInfoBean.discountType = '';
    this.addGroupRateInfoBean.discountScope = '';
    this.addGroupRateInfoBean.planNum = '';
    this.addGroupRateInfoBean.planAmt = '';
    this.thruDate = '';
    this.addGroupRateInfoBean.thruDate = '';
    this.addGroupRateInfoBean.schemeDesc = '';
    let ddate = moment().add(6, 'month').format('Y-MM-DD');
    this.thruDate = new Date(ddate);
  }
  colseThis() {
    this.outValue.emit(false);
    this.reset();
  }
  transDateN2(value) {
    if (value == '' || null) {
      return ''
    }
    let time = new Date(parseFloat(value));
    let year = time.getFullYear();
    let month = time.getMonth() + 1 + '';
    let data = time.getDate() + '';
    if (month.length < 2) {
      month = '0' + month;
    }
    if (data.length < 2) {
      data = "0" + data
    }
    let newtime = year + "-" + month + "-" + data;
    return newtime;
  }
  showData() {
    console.log(this.thruDate)
    let timethis = moment().add(2, 'year').format('Y-MM-DD');
    let now = moment().format('Y-MM-DD');
    let datethis = this.commfunc.transDateN(this.thruDate);
    if (datethis > timethis) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '优惠利率截至日期不能大于' + timethis });
      return;
    }
    if (datethis < now) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '优惠利率截至日期不能小于' + now });
      return;
    }

  }
  chooseType() {
    if (this.addGroupRateInfoBean.discountType == '1') {
      this.showtype = 1;
    } else if (this.addGroupRateInfoBean.discountType == '2') {
      this.showtype = 1;
    } else if (this.addGroupRateInfoBean.discountType == '3') {
      this.showtype = 1;
    } else if (this.addGroupRateInfoBean.discountType == '4') {
      this.showtype = 2;
    } else if (this.addGroupRateInfoBean.discountType == '5') {
      this.showtype = 3;
      this.addGroupRateInfoBean.discountScope = '';
    }
  }
  method() {
    console.log(this.dateshow)
    this.addGroupRateInfoBean.discountScope = this.dateshow;
  }
  fudu() {
    if (this.addGroupRateInfoBean.discountScope != '') {
      if (decimalInteger(null, this.addGroupRateInfoBean.discountScope)) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入数字!' });
        return;
      } else {
        if (this.addGroupRateInfoBean.discountType == '1') {
          this.dateshow = this.decimalPipe.transform(this.addGroupRateInfoBean.discountScope, '1.2-2');
          this.method();
        } else if (this.addGroupRateInfoBean.discountType == '2') {
          this.addGroupRateInfoBean.discountScope = this.addGroupRateInfoBean.discountScope + '%';
        } else if (this.addGroupRateInfoBean.discountType == '3') {
          this.addGroupRateInfoBean.discountScope = this.addGroupRateInfoBean.discountScope + '‰';
        }
      }

    }
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
}
